*{
	padding: 0;
	margin: 0;
}
body{
    overflow-x: hidden;
    background-color: #f7f7f7;
}
a,
button,
input{
	-moz-outline-style: none;
	outline-style: none;
}
a,
a:hover,
a:focus,
a:visited,
a:active{
	text-decoration: none;
	color: inherit;
}
ul{
    list-style: none;
}


