.card_wrapper{
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 0 0 22%;
        flex: 0 0 22%;

    min-width: 250px;
    min-height: 360px;
    overflow: hidden;

    -webkit-user-select: none;

       -moz-user-select: none;

        -ms-user-select: none;

            user-select: none;
    cursor: pointer;
    background-color: #CBECF7;
