.close_tag{
    position: absolute;
    top: 0;
    right: 0;
    height: 30px;
    width: 30px;

    color: rgba(0,0,0,0.7);
    font-size: 1.2em;
    background: none;
    border: none;

    cursor: pointer;
    -webkit-transition: .2s ease-in-out;
    -o-transition: .2s ease-in-out;
    transition: .2s ease-in-out;
}
.close_tag:hover{
    -webkit-transform: rotate(360deg);
